import React from "react";

const Button: React.FC<{ handler: React.MouseEventHandler<HTMLButtonElement>, children: string | JSX.Element }> = ({ handler, children }): JSX.Element => {
	return (
		<button className="w-full bg-primary rounded-md py-2 my-4 uppercase text-white font-semibold hover:bg-hover.primary" onClick={handler}>{children}</button>
	)
}

export {
	Button
}